/****  ****/

// 1. fade
.anim-items.fade {
    opacity: 0;
    transition: all 0.3s ease;
}

.fade.anim-active {
    opacity: 1;
    transition: all 1s ease 0.8s;
}

//fade  
.fade-second.anim-active {
    opacity: 1;
    transition: all 1s ease 1s;
}

// 2. Transform

// transform-left
.anim-items.transform-anim-top {
    opacity: 0;
    transform: translateY(-120%);
    transition: all 0.3s ease;
}

//transform-right

.anim-items.transform-anim-right {
    opacity: 0;
    transform: translateX(120%);
    transition: all 0.3s ease;
}

//transform-bottom

.anim-items.transform-anim-bottom {
    opacity: 0;
    transform: translateY(-120%);
    transition: all 0.3s ease;
}

//transform-left

.anim-items.transform-anim-left {
    transform: translateX(-120%);
    transition: all 0.3s ease;
    opacity: 0;
}

//  transform
.transform-anim-top.anim-active,
.transform-anim-right.anim-active,
.transform-anim-bottom.anim-active,
.transform-anim-left.anim-active
 {
    opacity: 1;
    transform: translate(0, 0);
    transition: all 0.8s ease 0.1s;
}

// rotate

.anim-items.rotate-left-top {
    opacity: 0;
    transform: rotate(-45deg);
    transition: all 0.8s ease 0.1s;
}

.anim-items.rotate-right-top {
    opacity: 0;
    transform: rotate(45deg);
    transition: all 0.8s ease 0.1s;
}

.anim-items.rotate-bottom-right {
    opacity: 0;
    transform: rotate(-45deg);
    transition: all 0.8s ease 0.1s;

}

.anim-items.rotate-bottom-left {

}

.rotate-right-top.anim-active,
.rotate-left-top.anim-active,
.rotate-bottom-right.anim-active,
.rotate-bottom-left.anim-active {

    opacity: 1;
    transform: rotate(0deg);
    transition: all 0.8s ease 0.1s;
}

//
@function multiple-box-shadow($n) {
  $value: '#{random(2000)}px #{random(2000)}px #FFF';
  @for $i from 2 through $n {
    $value: '#{$value} , #{random(2000)}px #{random(9000)}px #FFF'
  }
  @return unquote($value)
}

$shadows-small:  multiple-box-shadow(700);
$shadows-medium: multiple-box-shadow(200);
$shadows-big:    multiple-box-shadow(100);

.stars {
    width: 2px;
    display: block;
    height: 2px;
    background: transparent;
    box-shadow: $shadows-small;
    animation: animStar 50s linear infinite;

    &:after {
      content: " ";
      position: absolute;
      top: 2000px;
      width: 2px;
      height: 2px;
      background: transparent;
      box-shadow: $shadows-small;
    }
}


.stars2 {
    display: block;
  width: 3px;
  height: 3px;
  background: transparent;
  box-shadow: $shadows-medium;
  animation: animStar 100s linear infinite;

  &:after {
    content: " ";
    position: absolute;
    top: 3000px;
    width: 3px;
    height: 3px;
    background: transparent;
    box-shadow: $shadows-medium;
  }
}
.stars3 {
    display: block;
  width: 4px;
  height: 4px;
  background: transparent;
  box-shadow: $shadows-big;
  animation: animStar 150s linear infinite;

  &:after {
    content: " ";
    position: absolute;
    top: 5000px;
    width: 4px;
    height: 4px;
    background: transparent;
    box-shadow: $shadows-big;
  }
}

@keyframes animStar {
  from {
    transform: translateY(0px)
  }
  to {
    transform: translateY(-3000px)
  }
}

.space {
  position: absolute;
	top: 0;
  right: 0;
	left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}